<template>
  <v-card-text>
    <v-toolbar v-if="isAdmin.role != 'LOCAL'" color="error">
      <h3 class="white--text">Неправильная ссылка</h3>
    </v-toolbar>
    <v-row v-else>
      <v-col cols="12" lg="4">
        <v-form class="mb-16 mt-4">
          <v-card class="pa-4">
            <v-card-title class="">Информация о судье</v-card-title>
            <v-row>
              <v-col cols="12">
                <v-text-field v-model="editedItem.AUTOR_NAME" label="ФИО судьи" readonly dense hide-details></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-select v-model="editedItem.NUM_FIGURE" :items="figure" label="Фигура" item-text="name" item-value="id" dense hide-details readonly></v-select>
                <!-- <v-select v-model="editedItem.NUM_FIGURE" :items="figure" label="Фигура" item-text="name" item-value="id" readonly dense hide-details></v-select> -->
              </v-col>
            </v-row>
          </v-card>
        </v-form>

        <v-form v-if="isEditForm">
          <v-card class="pa-4">
            <v-card-title class="">Запись ошибок на фигурах</v-card-title>
            <v-row>
              <v-col cols="12">
                <!-- <v-text-field v-model="editedItem.NUM_PATH" label="Номер заезда" dense hide-details dark></v-text-field> -->
                <v-select v-model="editedItem.NUM_PATH" @change="checkKolViolation()" :items="numPath" label="Номер заезда" dense hide-details></v-select>
              </v-col>
              <v-col cols="12">
                <!-- <v-text-field v-model="editedItem.PEOPLE_CODE" label="ID участника" dense hide-details dark></v-text-field> -->
                <v-select v-model="editedItem.PEOPLE_CODE" @change="checkKolViolation()" :items="filterMemberList" item-value="id" label="Участник" dense hide-details>
                  <template #[`selection`]="{ item }">
                    {{ "Участник №" + item.id }}
                  </template>
                  <template #[`item`]="{ item }">
                    {{ "Участник №" + item.id }}
                  </template>
                </v-select>
              </v-col>

              <v-col>
                <v-row>
                  <v-col class="text-center" cols="3">
                    <v-btn v-if="changeViolationArr.find((i) => i == editedItem.NUM_FIGURE)" @click="changeKolError(-1)" color="error" icon fab><v-icon size="120">mdi-minus</v-icon></v-btn>
                  </v-col>
                  <v-col class="text-center" style="font-size: 40px" cols="6">
                    <h1 class="mt-5">{{ kolError }}</h1>
                  </v-col>
                  <v-col class="text-center" cols="3">
                    <v-btn v-if="changeViolationArr.find((i) => i == editedItem.NUM_FIGURE)" @click="changeKolError(1)" color="success" icon fab><v-icon size="120">mdi-plus</v-icon></v-btn>
                  </v-col>
                </v-row>
              </v-col>
              <v-col cols="12">
                <v-list class="pa-0 infoList" dense>
                  <v-list-item v-for="(item, index) in filterGroupByCategory" :key="index">
                    <v-list-item-content>
                      <v-list-item-title>
                        {{ editedItem.AUTOR_NAME }}
                      </v-list-item-title>
                      <v-list-item-subtitle> {{ item.name }} ({{ item.points }}) </v-list-item-subtitle>
                    </v-list-item-content>
                    <v-list-item-action>Кол-во: {{ item.kol }}</v-list-item-action>
                  </v-list-item>
                </v-list>
              </v-col>
              <template v-if="editedItem.NUM_PATH && editedItem.PEOPLE_CODE">
                <v-col v-for="item in filterBreachList" :key="item.id" class="py-0" cols="12">
                  <v-btn
                    @click="addFinePeople(item)"
                    :disabled="groupByCategory.filter((i) => i.id_violation == item.id).length == item.kol"
                    style="font-size: 10px"
                    class="my-1"
                    color="success"
                    height="35"
                    v-html="item.name"
                    block
                    small
                  ></v-btn>
                </v-col>
              </template>
            </v-row>
            <!-- <v-card-actions>
              <v-btn @click="addFinePeople()" class="mt-4" color="success">Отправить</v-btn>
            </v-card-actions> -->
          </v-card>
        </v-form>
      </v-col>
      <v-col cols="12" lg="8">
        <!-- <pre class="white--text">editedItem{{ editedItem }}</pre> -->
        <!-- <pre class="white--text">currentPeople{{ currentPeople }}</pre> -->
      </v-col>
    </v-row>
    <v-overlay :value="formLoading">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
  </v-card-text>
</template>

<script>
// import { db } from "../firebase";
import { mapState, mapActions, mapMutations } from "vuex";

import { supabase } from "../lib/supabaseClient";

export default {
  data() {
    return {
      editedItem: {},
      // figure: [], //Список фигур
      isAdmin: { role: "" },
      formLoading: false,
      breachList: [],
      groupByCategory: [],
      kolError: 1,
      changeViolationArr: [3, 5, 6, 7, 14],
    };
  },
  computed: {
    ...mapState("nsi", ["numPath", "membersList", "numFine", "currentPeople", "figure"]),
    tableHeight() {
      return window.innerHeight - 120;
    },
    isEditForm() {
      if (this.editedItem.AUTOR_NAME && this.editedItem.NUM_FIGURE) {
        return true;
      }
    },
    filterBreachList() {
      return this.figure.find((item) => item.id == this.editedItem.NUM_FIGURE).violations.sort((a, b) => (a.points > b.points ? 1 : -1));
      // return this.breachList.filter((item) => item.id_figure == this.editedItem.NUM_FIGURE);
    },
    filterGroupByCategory() {
      if (this.groupByCategory.length > 0) {
        const result = Object.values(
          this.groupByCategory.reduce((acc, { violations }) => {
            if (!acc[violations.id]) {
              acc[violations.id] = { id: violations.id, name: violations.name, points: violations.points, kol: 1 };
            } else {
              acc[violations.id].kol++;
            }
            return acc;
          }, {})
        );
        // return this.groupByCategory.filter((item) => item.violations.id_figure == this.editedItem.NUM_FIGURE);
        return result;
      } else {
        return [];
      }
    },
    filterMemberList() {
      return this.membersList.sort((a, b) => (a.id > b.id ? 1 : -1));
    },
  },
  watch: {
    currentPeople(from, to) {
      if (from) {
        this.$set(this.editedItem, "NUM_PATH", from.path_num);
        this.$set(this.editedItem, "PEOPLE_CODE", from.members_id);
      }
    },
  },
  // filters: {
  //   menu() {
  //     return this.navMenu.filters((item) => item.title != "Новости");
  //   },
  // },
  mounted() {
    this.getAdmin();

    if (this.currentPeople) {
      this.$set(this.editedItem, "NUM_PATH", this.currentPeople.path_num);
      this.$set(this.editedItem, "PEOPLE_CODE", this.currentPeople.members_id);
    }
  },
  methods: {
    ...mapActions(["SHOW_ERROR", "SHOW_SNACKBAR"]),
    ...mapMutations("nsi", ["MUTATE_CURRENT_PEOPLE"]),

    changeKolError(pVal) {
      this.kolError += pVal;
      this.kolError < 1 ? (this.kolError = 1) : (this.kolError = this.kolError);
    },

    async getAdmin() {
      try {
        this.formLoading = true;

        let { data, error, status } = await supabase.from("admins").select().eq("id", this.$route.query.id).single();

        if (error && status !== 406) throw error;

        if (data) {
          this.isAdmin = data;

          this.$set(this.editedItem, "AUTOR_NAME", this.isAdmin.fio);
          this.$set(this.editedItem, "NUM_FIGURE", this.isAdmin.id_figure);
        }
      } catch (error) {
        console.log(error.message);
      } finally {
        this.formLoading = false;
      }
      //Автоматическое получение участника
      try {
        const curmem = supabase
          .channel("custom-update-channel")
          .on("postgres_changes", { event: "UPDATE", schema: "public", table: "curmem" }, (payload) => {
            console.log("Change received!", payload);
            this.MUTATE_CURRENT_PEOPLE(payload.new);
            alert("Обновился участник");
          })
          .subscribe();
      } catch (error) {
        console.log(error);
      }
    },

    async checkKolViolation() {
      if (this.editedItem.NUM_PATH && this.editedItem.PEOPLE_CODE) {
        try {
          this.formLoading = true;

          let { data, error, status } = await supabase
            .from("main_table")
            .select(`violations (id, id_figure, name, points)`)
            .match({ id_admin: this.$route.query.id, id_member: this.editedItem.PEOPLE_CODE, num_race: this.editedItem.NUM_PATH });
          // this.groupByCategory = data.reduce((group, product) => {
          //   const { id_violation } = product;
          //   group[id_violation] = group[id_violation] ?? [];
          //   group[id_violation].push(product);
          //   return group;
          // }, {});
          this.groupByCategory = data;
          // .groupBy("num_race");
          // .match("id_member", this.editedItem.PEOPLE_CODE)
          // .match(");
        } catch (error) {
          alert(error.message);
        } finally {
          this.formLoading = false;
        }
      }
    },

    async addFinePeople(pItem) {
      // this.SHOW_ERROR("Редактирование запрещено");
      // return;

      if (!this.editedItem.NUM_PATH || !this.editedItem.PEOPLE_CODE) {
        this.SHOW_ERROR("Нет текущего участника");
        return;
      }

      const confirm = await this.$root.$confirm("Внимание!", `Добавить:<br> ${pItem.name} <br>Кол-во: ${this.kolError} <br>  Участнику №${this.editedItem.PEOPLE_CODE}`, { color: "info" });
      if (!confirm) return;
      try {
        this.formLoading = true;

        for (let i = 0; i < this.kolError; i++) {
          const { error } = await supabase
            .from("main_table")
            .insert({ id_admin: this.$route.query.id, num_race: this.editedItem.NUM_PATH, id_member: this.editedItem.PEOPLE_CODE, id_violation: pItem.id });
          if (error) throw error;
        }
        this.SHOW_SNACKBAR("Штраф добавлен участнику");
        this.kolError = 1;
        this.checkKolViolation();
      } catch (error) {
        this.SHOW_ERROR(error);
      } finally {
        this.formLoading = false;
      }

      // this.$cookies.set("NAME_AUTOR", this.editedItem.AUTOR_NAME, 60);
      // this.$cookies.set("NUM_FIGURE", this.editedItem.NUM_FIGURE, 60);
    },
  },
};
</script>

