              </thead>
              <tbody>
                <tr v-for="(item, index) in filterMembersList" :key="index" :style="{ '--fontSize': isMobile ? '0.9em' : '1.4em' }">
                  <td class="px-2">
                    <div
                      :style="{
                        background:
                          index == 0
                            ? 'linear-gradient(90deg,gold 5%, transparent 90%)'
                            : index == 1
                            ? 'linear-gradient(90deg,silver 5%, transparent 65%)'
                            : index == 2
                            ? 'linear-gradient(90deg,tan 5%, transparent 40%)'
                            : '',
                        width: '100%',
                        height: '100%',
                      }"
                      class="d-flex justify-space-around align-center"
                    >
                      <v-icon v-if="newBestResultGreen.indexOf(index) >= 0" color="success" large left>mdi-menu-up</v-icon>
                      <v-icon v-else-if="newBestResultRed.indexOf(index) >= 0" color="error" large left>mdi-menu-down</v-icon>
                      <v-icon v-else color="secondary" large left>mdi-menu-right</v-icon>
