                      <!-- {{ index + 1 > 3 ? index + 1 : "" }} -->
                    </div>
                  </td>
                  <td @click="indexUser = index" class="px-2 text-center text-decoration-underline" style="cursor: pointer">{{ item.fio_f }} {{ item.fio_i }} {{ item.fio_o }}</td>
                  <td class="px-2 text-center">{{ item.job }}</td>
                  <td class="px-2 text-center">{{ item.teor_1 }}</td>
                  <td class="px-2 text-center">{{ item.teor_2 }}</td>
                  <template v-if="item.path1_error < 5000">
                    <td class="px-2 text-center">
                      {{ item.path1_time | minutesSeconds }}
                    </td>
                    <td @click="getMoreInfo(item.id, 1)" class="px-2 text-center text-decoration-underline" style="cursor: pointer">
                      {{ item.path1_error }}
                    </td>
                    <td class="px-2 text-center">
                      <span :style="{ color: item.path2_time + item.path2_error >= item.path1_time + item.path1_error ? 'green' : '#ee3124' }">
                        {{ Math.round((item.path1_time + item.path1_error + item.teor_1 + item.teor_2) * 100) / 100 }}
                      </span>
                    </td>
                  </template>
                  <td v-else @click="getMoreInfo(item.id, 1)" colspan="3" class="px-2 text-center">Не пройдено</td>
                  <template v-if="item.path2_error < 5000">
                    <td class="px-2 text-center">
                      {{ item.path2_time | minutesSeconds }}
                    </td>
                    <td @click="getMoreInfo(item.id, 2)" class="px-2 text-center text-decoration-underline" style="cursor: pointer">
                      {{ item.path2_error }}
                    </td>
                    <td class="px-2 text-center">
                      <span :style="{ color: item.path2_time + item.path2_error < item.path1_time + item.path1_error ? 'green' : '#ee3124' }">
                        {{ Math.round((item.path2_time + item.path2_error + item.teor_1 + item.teor_2) * 100) / 100 }}
                      </span>
                    </td>
                  </template>
                  <td v-else @click="getMoreInfo(item.id, 2)" colspan="3" class="px-2 text-center">Не пройдено</td>
                  <!-- <td :style="{ '--all': filterMembersList.length + 's', '--numCol': '1s', '--numRow': index, '--color': color }" class="px-4 text-center">
                {{ item.path1_points + item.path2_points - item.path1_error - item.path2_error }}
              </td> -->
                </tr>
              </tbody>
            </table>
          </figure>
        </div>
      </v-col>

      <v-col class="fadeNew pb-0" :key="filterCurrentMembers.fio_f" cols="12" lg="4">
        <div class="fadeOut">
          <h2 class="title">ИНФОРМАЦИЯ УЧАСТНИКА</h2>
          <div class="people" :style="{ minHeight: tableHeight }">
            <div class="people__roundFoto">
              <span></span>
              <span></span>
              <div class="people__roundFoto-wrapper">
                <v-img :src="require(`../assets/${filterCurrentMembers.num_members}.jpg`)" contain></v-img>
                <!-- <v-img :src="require(`../assets/no-photo.jpg`)" contain></v-img> -->
              </div>
            </div>
            <h2 class="people__name">{{ filterCurrentMembers.fio_f }} {{ filterCurrentMembers.fio_i }}</h2>
            <h2 class="people__secondName">{{ filterCurrentMembers.fio_o }}</h2>
            <h2 class="people__profession">{{ filterCurrentMembers.job_prof }}</h2>
            <v-list>
              <!-- <v-list-item>
                <v-list-item-title>Возраст</v-list-item-title>
                <v-list-item-subtitle>{{ filterCurrentMembers.age }}</v-list-item-subtitle>
              </v-list-item>
              <v-divider></v-divider>
              <v-list-item>
                <v-list-item-title>Стаж</v-list-item-title>
                <v-list-item-subtitle>{{ filterCurrentMembers.job_year }}</v-list-item-subtitle>
              </v-list-item> -->
              <v-divider></v-divider>
              <v-list-item>
                <v-list-item-title>ОСТ</v-list-item-title>
                <v-list-item-subtitle>{{ filterCurrentMembers.job }} / {{ filterCurrentMembers.filial }}</v-list-item-subtitle>
              </v-list-item>
            </v-list>
            <v-divider></v-divider>
            <!-- <v-data-table class="ma-4" style="font-size: 1.6em" :items="figure" :items-per-page="-1" hide-default-footer hide-default-header dense> -->
            <table class="v-data-table" style="font-size: 1.6em; width: 95%; margin: 12px auto">
              <thead class="v-data-table-header">
                <tr :style="{ fontSize: isMobile ? '1em' : '1.8em' }">
                  <th class="text-center pa-2" style="width: 50%">Фигура</th>
                  <th class="text-center pa-2" style="width: 25%">Заезд 1</th>
                  <th class="text-center pa-2" style="width: 25%">Заезд 2</th>
                </tr>
              </thead>
              <tbody>
                <template v-for="item in filterCurrentMembersError">
                  <tr :key="item.NAME" v-if="item.PATH1 > 0 || item.PATH2 > 0" :style="{ fontSize: isMobile ? '0.9em' : '1.6em' }" class="trNone">
                    <td class="text-center" style="width: 50%; height: 36px">{{ item.NAME }}</td>
                    <td class="text-center" style="width: 25%">{{ item.PATH1 > 5000 ? "-" : item.PATH1 }}</td>
                    <td class="text-center" style="width: 25%">{{ item.PATH2 > 5000 ? "-" : item.PATH2 }}</td>
                  </tr>
                </template>
                <tr :style="{ fontSize: isMobile ? '0.9em' : '1.6em' }">
                  <td style="width: 50%; color: #005596 !important; height: 36px" class="px-2 text-center">Итого</td>
                  <td style="width: 25%; color: #005596 !important" class="px-2 text-center">{{ getSumRace(1) }}</td>
                  <td style="width: 25%; color: #005596 !important" class="px-2 text-center">{{ getSumRace(2) }}</td>
                </tr>
              </tbody>
            </table>
            <!-- <line-chart ref="chartPie" id="chart" :key="filterCurrentMembers.id"></line-chart> -->

            <!-- <div class="people__nowresult">
              <div class="people__nowresult-header">
                <div class="title">Заезд 1</div>
                <div class="title">Заезд 2</div>
              </div>
              <div class="people__nowresult-text">
                <div class="text text_title">Время</div>
              </div>
              <div class="people__nowresult-text">
                <div class="text">{{ filterCurrentMembers.path1_time | minutesSeconds }}</div>
                <div class="text">{{ filterCurrentMembers.path2_time | minutesSeconds }}</div>
              </div>
              <div class="people__nowresult-text">
                <div class="text text_title">Время с учетом штрафов</div>
              </div>
              <div class="people__nowresult-text">
                <div v-if="filterCurrentMembers.path1_error < 5000" class="text">{{ (filterCurrentMembers.path1_time + filterCurrentMembers.path1_error) | minutesSeconds }}</div>
                <div v-else class="text">Не пройдено</div>
                <div v-if="filterCurrentMembers.path2_error < 5000" class="text">{{ (filterCurrentMembers.path2_time + filterCurrentMembers.path2_error) | minutesSeconds }}</div>
                <div v-else class="text">Не пройдено</div>
              </div>
              <v-img class="my-4" style="margin: 0 auto" :src="require(`../assets/qr.jpg`)" width="200"></v-img>
            </div> -->
          </div>
        </div>
      </v-col>
    </v-row>
    <v-overlay :value="formLoading">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
    <v-dialog v-model="dialogInfo" max-width="700">
      <v-card class="editJob">
        <v-card-title class="primary white--text" style="border-radius: 0 0 10px 10px">
          <span class="text-h5">ИНФОРМАЦИЯ </span>
        </v-card-title>
        <v-divider class="my-4 mx-6"></v-divider>
        <v-card-text>
          <v-list dense>
            <v-list-item v-for="item in figure" :key="item.id" style="border-bottom: 1px solid black">
              <v-list-item-content> {{ item.name }} </v-list-item-content>
              <v-list-item-action>
                <span v-if="currentMoreInfo.find((i) => i.violations.id_figure == item.id)" class="success--text">Отмечено</span>
                <span v-else class="error--text">Не отмечено</span>
              </v-list-item-action>
            </v-list-item>
          </v-list>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="secondary darken-1" @click="dialogInfo = false"> <v-icon small>mdi-arrow-left</v-icon>Закрыть </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card-text>
</template>

<script>
// import { db } from "../firebase";
import { mapState, mapMutations } from "vuex";

import { supabase } from "../lib/supabaseClient";
import LineChart from "./LineChart.vue";

export default {
  components: { LineChart },

  data() {
    return {
      color: "rgba(0, 110, 255, 0.3)", //Рандомный цвет
      userRef: null,
      formLoading: false,
      indexUser: 0,
      errorMembers: [], //Список ошибок
      newBestResultGreen: [],
      newBestResultRed: [],
      laViolation: [],
      dialogInfo: false, //Окно для подробной информации
      currentMoreInfo: [],
    };
  },

  filters: {
    minutesSeconds(val) {
      if (val) {
        const min = Math.trunc(val / 60);
        const sec = val % 60;
        return `${min < 10 ? "0" + min : min}:${sec < 10 ? "0" + sec.toFixed(2) : sec.toFixed(2)}`;
      } else {
        return "00:00.0";
      }
    },
  },
  watch: {
    indexUser() {
      this.getErrorMembers();
    },
  },

  computed: {
    ...mapState("nsi", ["resultList", "membersList", "currentPeople", "figure"]),
    tableHeight() {
      return window.innerHeight - 70 + "px";
    },
    isMobile() {
      if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|BB|PlayBook|IEMobile|Windows Phone|Kindle|Silk|Opera Mini/i.test(navigator.userAgent)) {
        return true;
      } else return false;
    },
    isOut() {
      return this.$route.query.isOut == 1;
    },
    filterCurrentMembers() {
      return this.membersList[this.indexUser];
    },
    filterMembersList() {
      return this.membersList; //.filter((item) => item.path1_points > 0 || item.path2_points > 0);
    },
    filterCurrentMembersError() {
      var tempArr = [];
      var tempObj = { NAME: "", PATH1: 0, PATH2: 0 };
      for (let i = 0; i < this.figure.length; i++) {
        const filtNumRace1 = this.errorMembers.filter((item) => item.num_race == 1 && item.violations.id_figure == this.figure[i].id);
        tempObj.PATH1 = filtNumRace1.reduce((acc, cur) => acc + cur.violations.points, 0);
        const filtNumRace2 = this.errorMembers.filter((item) => item.num_race == 2 && item.violations.id_figure == this.figure[i].id);
        tempObj.PATH2 = filtNumRace2.reduce((acc, cur) => acc + cur.violations.points, 0);
        tempObj.NAME = this.figure[i].name;
        tempArr.push(tempObj);
        tempObj = {};
      }
      return tempArr;
    },

    // menu() {
    //   return this.navMenu.filter((item) => item.title != "Новости");
    // },
  },
  // filters: {
  //   menu() {
  //     return this.navMenu.filters((item) => item.title != "Новости");
  //   },
  // },
  mounted() {
    this.getCurrentUser();
    this.newColor();
    if (!this.isMobile) {
      this.updateTable();
    }
  },

  methods: {
    ...mapMutations("nsi", ["MUTATE_MEMBERS"]),

    // add() {
    //   console.log(this.navMenu);
    // },
    async getMoreInfo(pId, pNum) {
      this.dialogInfo = true;
      const { data, error } = await supabase.from("main_table").select(`id, violations (id, id_figure)`).match({ id_member: pId, num_race: pNum });
      this.currentMoreInfo = [...data];
    },
    async getCurrentUser() {
      setInterval(() => {
        if (this.indexUser < this.membersList.length - 1) {
          this.indexUser++;
        } else {
          this.indexUser = 0;
        }
      }, 20000);
    },
    async getErrorMembers() {
      const { data, error } = await supabase.from("main_table").select(`id, num_race, violations (id, name, points, id_figure)`).match({ id_member: this.membersList[this.indexUser].id });
      this.errorMembers = data;
    },
    filterRace(pNum, pId) {
      const filtNumRace = this.errorMembers.filter((item) => item.num_race == pNum && item.violations.id_figure == pId);
      return filtNumRace.reduce((acc, cur) => acc + cur.violations.points, 0);
    },
    getSumRace(pNum) {
      const filtNumRace = this.errorMembers.filter((item) => item.num_race == pNum);
      const res = filtNumRace.reduce((acc, cur) => acc + cur.violations.points, 0);
      return res > 5000 ? "Отменен" : res;
    },
    async updateMembers() {
      this.userRef = 2;

      this.userRef = 3;

      this.getUser();
    },
    async updateTable() {
      // const { data, error } = await supabase.from("violations").select();
      // this.laViolation = data;
      setInterval(() => {
        this.refreshMainTable();
      }, 60000);
    },
    async refreshMainTable() {
      try {
        const { data, error } = await supabase.from("violations").select();
        const laViolations = data;
        if (error) throw error;

        for (let i = 0; i < this.membersList.length; i++) {
          try {
            const { data, error } = await supabase.from("main_table").select(`id, num_race, id_violation`).match({ id_member: this.membersList[i].id });
            if (error) throw error;
            var points = { path1: 0, path2: 0 };
            for (let j = 0; j < data.length; j++) {
              if (data[j].num_race == 1) {
                points.path1 += laViolations.find((item) => item.id == data[j].id_violation).points;
              } else {
                points.path2 += laViolations.find((item) => item.id == data[j].id_violation).points;
              }
            }
          } catch (error) {
            throw error;
            // this.SHOW_ERROR(error);
          } finally {
            const { error } = await supabase.from("members").update({ path1_error: points.path1, path2_error: points.path2 }).eq("id", this.membersList[i].id);
            if (error) throw error;
          }
        }
      } catch (error) {
        console.log(error);
      } finally {
        this.getMembers();
      }
    },
    async getMembers() {
      const currentUser = await this.$GF.getMembers(); //await supabase.from("members").select();

      const vm = this;
      this.newBestResultGreen = [];
      this.newBestResultRed = [];
      this.membersList.map((obj, index) => {
        const tempIndex = currentUser.data.findIndex((item) => item.id == obj.id);
        if (index > tempIndex) {
          vm.newBestResultGreen.push(tempIndex);
        } else if (index < tempIndex) {
          vm.newBestResultRed.push(tempIndex);
        }
      });
      // setTimeout(() => {
      //   this.newBestResult = [];
      // }, 5000);
      this.MUTATE_MEMBERS(currentUser.data);
    },
    newColor() {
      setInterval(() => {
        this.color = `rgba(${Math.floor(Math.random() * 256)},${Math.floor(Math.random() * 256)},${Math.floor(Math.random() * 256)}, 0.3)`;
        // this.color = "#" + (Math.random().toString(16) + "000000").substring(2, 4).toUpperCase() + "FBD6";
      }, 60000);
    },
  },
};
</script>

<style lang="scss" scoped>
/* Shine */
.shine-animation figure {
  position: relative;
  overflow: hidden;
}
.shine-animation figure::before {
  position: absolute;
  top: 0;
  left: -80%;
  z-index: 2;
  display: block;
  content: "";
  width: 30%;
  height: 100%;
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, var(--color) 100%);
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, var(--color) 100%);
  -webkit-transform: skewX(-25deg);
  transform: skewX(-25deg);
  -webkit-animation: shine 60s infinite;
  animation: shine 60s infinite;
  // animation-delay: 5s;
}
@-webkit-keyframes shine {
  95% {
    left: -80%;
  }
  100% {
    left: 125%;
  }
}
@keyframes shine {
  95% {
    left: -80%;
  }
  100% {
    left: 125%;
  }
}
//
