  text-align: center;
  margin: 15px 0;
  font-size: 2.5em !important;
  color: #000;
}
.v-card__text {
  .row {
    .col {
      color: white;
    }
  }
}

table th + th {
  border-left: 2px solid white;
}
table td + td {
  border-left: 2px solid #9d9e9e;
}
.v-data-table {
  background: #fff !important; //#575a7c !important;
  color: #000 !important; //#fafbd6 !important;
  border-collapse: collapse;
  thead {
    tr {
      th {
        background: #005596 !important; //#292b42 !important;
        color: #fff !important;
        font-size: var(--fontSize) !important;
        padding: 0 !important;
        height: 40px;
      }
    }
  }
  tbody {
    tr {
      td {
        font-size: var(--fontSize) !important;
        padding: 0 !important;
        border-bottom: 2px solid #292b42;
        border-right: 0px;
        border-top: 0px;
      }
    }
  }
}

.fadeNew {
  // opacity: 0;

  animation: show 1s forwards;
  @keyframes show {
    0% {
      transform: scale(0.1);
      opacity: 0;
    }
    100% {
      transform: scale(1);
      opacity: 1;
    }
  }
}
.fadeOut {
  position: relative;
  animation: out 1s 19s forwards;
  @keyframes out {
    0% {
      transform: scalle(1);
      opacity: 1;
    }
    100% {
      transform: scale(0.1);
      opacity: 0;
    }
  }
}
.people {
  // background: #dfdfdf !important; //#575a7c !important;

  font-size: 0.8em;
  border: 1px solid #ee3124;
  // box-shadow: 0 0 10px 1px orange inset;
  animation: borderPeople 2s infinite alternate;
  padding-top: 35px;
  @keyframes borderPeople {
    0% {
      box-shadow: 0 0 1px 1px #ee3124 inset;
    }

    100% {
      box-shadow: 0 0 15px 1px #ee3124 inset;
    }
  }
  &__roundFoto {
    position: relative;
    width: 250px;
    height: 250px;
    margin: 0 auto;
    &-wrapper {
      position: relative;
      box-shadow: 0 0 15px 5px #dfdfdf;
      width: 250px;
      height: 250px;
      border-radius: 50%;
      background: #fff;
      margin: 0 auto;
      overflow: hidden;
    }
    // .forward {
    // }
    // .back {
    // }
    span {
      position: absolute;
      top: 0;
      left: 0;
      // background: red;
      width: 250px;
      height: 250px;
      &:not(:last-child) {
        animation: 9s linear 0s normal none infinite running spinSpan;
      }
      &:first-child {
        animation: 4s linear reverse 0s none infinite running spinSpan;
      }
      &::before {
        content: "";
        position: absolute;
        top: 2px;
        left: 100px;
        width: 50px;
        height: 2px;
        border-radius: 50%;
        background: #9d9e9e;
        box-shadow: 0 0 30px 10px #9d9e9e;
      }
      &::after {
        content: "";
        position: absolute;
        bottom: 2px;
        left: 100px;
        width: 50px;
        height: 2px;
        border-radius: 50%;
        background: #9d9e9e;
        box-shadow: 0 0 30px 10px #9d9e9e;
