<template>
  <v-card-text>
    <v-toolbar v-if="isAdmin.role != 'MAIN'" color="error">
      <h3 class="white--text">Неправильная ссылка</h3>
    </v-toolbar>
    <v-row v-else>
      <v-col cols="12" lg="4">
        <v-form class="mb-16 mt-4">
          <v-card class="pa-4">
            <v-card-title>Текущий участник</v-card-title>
            <v-row>
              <v-col cols="12">
                <v-select v-model="createUser.NUM_PATH" :items="numPath" label="Номер заезда" dense hide-details></v-select>
              </v-col>
              <v-col cols="12">
                <v-select v-model="createUser.PEOPLE_CODE" :items="filterMemberList" item-value="id" label="Участник" dense hide-details>
                  <template #[`selection`]="{ item }">
                    {{ "Участник №" + item.id }}
                  </template>
                  <template #[`item`]="{ item }">
                    {{ "Участник №" + item.id }}
                  </template>
                </v-select>
              </v-col>
            </v-row>
            <v-card-actions>
              <v-btn @click="addCurrentPeople()" class="mt-4" color="success">Сохранить участника</v-btn>
            </v-card-actions>
          </v-card>
        </v-form>
      </v-col>
      <v-col cols="12" lg="4">
        <v-form class="mb-16 mt-4">
          <v-card class="pa-4">
            <v-card-title class="">Финиш участника</v-card-title>
            <v-row>
              <v-col cols="12">
                <v-select v-model="editedItem.NUM_PATH" :items="numPath" label="Номер заезда" dense hide-details></v-select>
              </v-col>
              <v-col cols="12">
                <!-- <v-text-field v-model="editedItem.PEOPLE_CODE" label="ID участника" dense hide-details dark></v-text-field> -->
                <v-select v-model="editedItem.PEOPLE_CODE" :items="filterMemberList" item-value="id" label="Участник" dense hide-details>
                  <template #[`selection`]="{ item }">
                    {{ "Участник №" + item.id }}
                  </template>
                  <template #[`item`]="{ item }">
                    {{ "Участник №" + item.id }}
                  </template>
                </v-select>
              </v-col>
              <!-- <v-col cols="12">
                <v-text-field v-model="editedItem.POINTS" type="number" label="Очки за заезд" dark dense hide-details></v-text-field>
              </v-col> -->
              <v-col cols="4">
                <v-text-field v-model="editedItem.TIME_MIN" type="number" label="Минуты" dense hide-details></v-text-field>
              </v-col>
              <v-col cols="4">
                <v-text-field v-model="editedItem.TIME_SEC" type="number" label="Секунды" dense hide-details></v-text-field>
              </v-col>
              <v-col cols="4">
                <v-text-field v-model="editedItem.TIME_MIL" type="number" label="Милисекунды" dense hide-details></v-text-field>
              </v-col>
            </v-row>
            <v-card-actions>
              <v-btn @click="addFinishTimePeople()" class="mt-4" color="success">Сохранить результат</v-btn>
            </v-card-actions>
          </v-card>
        </v-form>
      </v-col>
      <v-col cols="12" lg="4">
        <v-form class="mb-16 mt-4">
          <v-card class="pa-4">
            <v-card-title class="">Результаты теории</v-card-title>
            <v-row>
              <v-col cols="12">
                <v-select v-model="editedItem.NUM_TEOR" :items="numPath" label="Номер теории" dense hide-details></v-select>
              </v-col>
              <v-col cols="12">
                <!-- <v-text-field v-model="editedItem.PEOPLE_CODE" label="ID участника" dense hide-details dark></v-text-field> -->
                <v-select v-model="editedItem.PEOPLE_CODE" :items="filterMemberList" item-value="id" label="Участник" dense hide-details>
                  <template #[`selection`]="{ item }">
                    {{ "Участник №" + item.id }}
                  </template>
                  <template #[`item`]="{ item }">
                    {{ "Участник №" + item.id }}
                  </template>
                </v-select>
              </v-col>
              <!-- <v-col cols="12">
                <v-text-field v-model="editedItem.POINTS" type="number" label="Очки за заезд" dark dense hide-details></v-text-field>
              </v-col> -->
              <v-col cols="12">
                <v-text-field v-model="editedItem.POINT_TEOR" type="number" label="Баллы" dense hide-details></v-text-field>
              </v-col>
            </v-row>
            <v-card-actions>
              <v-btn @click="addTeorPeople()" class="mt-4" color="success">Сохранить результат</v-btn>
            </v-card-actions>
          </v-card>
        </v-form>
      </v-col>
    </v-row>
    <v-overlay :value="formLoading">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
  </v-card-text>
</template>

<script>
// import { db } from "../firebase";
import { mapState, mapActions, mapMutations } from "vuex";

import { supabase } from "../lib/supabaseClient";

export default {
  data() {
    return {
      editedItem: {}, //Редактирование участника
      createUser: {}, //Создание участника
      isAdmin: { role: "" },
      formLoading: false,
    };
  },
  computed: {
    ...mapState("nsi", ["figure", "numPath", "membersList", "numFine", "currentPeople"]),
    tableHeight() {
      return window.innerHeight - 120;
    },
    isEditForm() {
      if (this.editedItem.AUTOR_NAME && this.editedItem.NUM_FIGURE) {
        return true;
      }
    },
    filterMemberList() {
      return this.membersList.sort((a, b) => (a.id > b.id ? 1 : -1));
    },
    // menu() {
    //   return this.navMenu.filter((item) => item.title != "Новости");
    // },
  },
  // filters: {
  //   menu() {
  //     return this.navMenu.filters((item) => item.title != "Новости");
  //   },
  // },
  mounted() {
    // if (this.$cookies.get("NAME_AUTOR")) {
    //   this.$set(this.editedItem, "AUTOR_NAME", this.$cookies.get("NAME_AUTOR"));
    // }
    // if (this.$cookies.get("NUM_FIGURE")) {
    //   this.$set(this.editedItem, "NUM_FIGURE", Number(this.$cookies.get("NUM_FIGURE")));
    // }

    this.getAdmin();
    if (this.currentPeople) {
      this.$set(this.editedItem, "NUM_PATH", this.currentPeople.path_num);
      this.$set(this.editedItem, "PEOPLE_CODE", this.currentPeople.members_id);
    }

    // this.checkUser();
  },
  methods: {
    ...mapMutations("nsi", ["MUTATE_CURRENT_PEOPLE"]),
    ...mapActions(["SHOW_ERROR", "SHOW_SNACKBAR"]),

    async getAdmin() {
      this.formLoading = true;
      const admin = await supabase.from("admins").select().match({ id: this.$route.query.id });
      // this.MUTATE_ADMIN(admin.data);
      this.isAdmin = admin.data[0];

      this.formLoading = false;
    },
    async getUser() {
      this.formLoading = true;

      await db
        .collection("admins")
        .doc(this.$route.query.id)
        .get()
        .then((snapshot) => {
          this.isAdmin = snapshot.data();
        })
        .catch((error) => {
          console.error(error);
        });
      setTimeout(() => {
        console.log(1);
      }, 1500);
      this.$set(this.editedItem, "NUM_PATH", this.currentPeople.path_num);
      this.$set(this.editedItem, "PEOPLE_CODE", this.currentPeople.members_id);
      this.$set(this.createUser, "NUM_PATH", this.currentPeople.path_num);
      this.$set(this.createUser, "PEOPLE_CODE", this.currentPeople.members_id);
      this.$set(this.editedItem, "TIME_MIN", 0);
      this.$set(this.editedItem, "TIME_SEC", 0);
      this.formLoading = false;
    },
    async addCurrentPeople() {
      this.formLoading = true;
      const res = await supabase
        .from("curmem")
        .update({ members_id: this.createUser.PEOPLE_CODE, path_num: this.createUser.NUM_PATH })
        // .update({ path1_points: Number(this.editedItem.POINTS), path1_time: this.editedItem.TIME })
        .eq("id", 1);
      console.log(res);
      this.formLoading = false;
    },

    // async addCurrentPeople() {
    //   if (!this.createUser.NUM_PATH || !this.createUser.PEOPLE_CODE) {
    //     this.SHOW_ERROR("Неправильно указан участник");
    //     return;
    //   }
    //   const confirm = await this.$root.$confirm("Внимание!", "Сохранить текущего участника?", { color: "info" });
    //   if (!confirm) return;
    //   this.MUTATE_CURRENT_PEOPLE({ PEOPLE_CODE: this.createUser.PEOPLE_CODE, NUM_PATH: this.createUser.NUM_PATH });
    //   this.checkUser();
    // },

    async addFinishTimePeople() {
      if (!this.editedItem.NUM_PATH || !this.editedItem.PEOPLE_CODE) {
        this.SHOW_ERROR("Нет текущего участника");
        return;
      }
      // if (!this.editedItem.POINTS) {
      //   this.SHOW_ERROR("Не указаны быллы за заезд");
      //   return;
      // }
      var time =
        Number(this.editedItem.TIME_MIN ? this.editedItem.TIME_MIN : 0) * 60 +
        Number(this.editedItem.TIME_SEC ? this.editedItem.TIME_SEC : 0) +
        Number(this.editedItem.TIME_MIL ? this.editedItem.TIME_MIL : 0) / 100;
      console.log(time);
      if (time <= 0) {
        this.SHOW_ERROR("Не указано время");
        return;
      }
      const confirm = await this.$root.$confirm("Внимание!", `Сохранить результат<br> участника №${this.editedItem.PEOPLE_CODE}?`, { color: "info" });
      if (!confirm) return;
      this.$set(this.editedItem, "TIME", time);
      try {
        var res;
        if (this.editedItem.NUM_PATH == 1) {
          res = await supabase
            .from("members")
            .update({ path1_points: this.editedItem.TIME, path1_time: this.editedItem.TIME })
            // .update({ path1_points: Number(this.editedItem.POINTS), path1_time: this.editedItem.TIME })
            .eq("id", this.editedItem.PEOPLE_CODE);
        } else {
          res = await supabase
            .from("members")
            .update({ path2_points: this.editedItem.TIME, path2_time: this.editedItem.TIME })
            // .update({ path2_points: Number(this.editedItem.POINTS), path2_time: this.editedItem.TIME })
            .eq("id", this.editedItem.PEOPLE_CODE);
        }

        if (res.error) throw res.error;
      } catch (error) {
        alert(error);
      } finally {
        this.editedItem = {};
        // this.refreshMainTable();
      }

      // this.checkUser();
    },
    async addTeorPeople() {
      if (!this.editedItem.NUM_TEOR || !this.editedItem.PEOPLE_CODE) {
        this.SHOW_ERROR("Нет текущего участника");
        return;
      }
      if (!this.editedItem.POINT_TEOR) {
        this.SHOW_ERROR("Не указаны быллы за теорию");
        return;
      }

      const confirm = await this.$root.$confirm("Внимание!", `Сохранить результат<br> участника №${this.editedItem.PEOPLE_CODE}?`, { color: "info" });
      if (!confirm) return;
      try {
        var res;
        if (this.editedItem.NUM_TEOR == 1) {
          res = await supabase.from("members").update({ teor_1: this.editedItem.POINT_TEOR }).eq("id", this.editedItem.PEOPLE_CODE);
        } else {
          res = await supabase.from("members").update({ teor_2: this.editedItem.POINT_TEOR }).eq("id", this.editedItem.PEOPLE_CODE);
        }
        if (res.error) throw res.error;
      } catch (error) {
        alert(error);
      } finally {
        this.editedItem = {};
        // this.refreshMainTable();
      }

      // this.checkUser();
    },
  },
};
</script>

