import Vue from "vue";
import { supabase } from "./../lib/supabaseClient";

const GF = {
  async getMembers() {
    const currentUser = await supabase.from("members").select();
    console.log(currentUser);
    currentUser.data.sort(function (a, b) {
      var path1a = 0;
      var path2a = 0;
      var path1b = 0;
      var path2b = 0;
      if (a.path1_error > 5000) {
        path1a = -10000;
      } else {
        path1a = 0 - a.path1_points - a.path1_error - a.teor_1 - a.teor_2;
      }
      if (a.path2_error > 5000) {
        path2a = -10000;
      } else {
        path2a = 0 - a.path2_points - a.path2_error - a.teor_1 - a.teor_2;
      }
      if (b.path1_error > 5000) {
        path1b = -10000;
      } else {
        path1b = 0 - b.path1_points - b.path1_error - b.teor_1 - b.teor_2;
      }
      if (b.path2_error > 5000) {
        path2b = -10000;
      } else {
        path2b = 0 - b.path2_points - b.path2_error - b.teor_1 - b.teor_2;
      }
      // console.log(a.id, path1a, path2a, b.id, path1b, path2b);
      // if (path1a + path2a < path1b + path2b) {
      //   return 1;
      // } else {
      //   return -1;
      // }
      if (path1a == 0 && path2a == 0) {
        return 1;
      }
      if (path1b == 0 && path2b == 0) {
        return -1;
      }
      var bestPath1 = (path1a > path2a && path1a != 0 && path1a != -10000) || path2a == -10000 ? path1a : path2a == 0 ? path1a : path2a;
      var bestPath2 = (path1b > path2b && path1b != 0 && path1b != -10000) || path2b == -10000 ? path1b : path2b == 0 ? path1b : path2b;
      // console.log(a.id, bestPath1, b.id, bestPath2);

      if (bestPath1 == 0 && bestPath2 < 0) {
        return 1;
      } else if (bestPath2 == 0 && bestPath1 < 0) {
        return -1;
      } else if (bestPath1 == bestPath2 && a.id < b.id) {
        return 1;
      } else if (bestPath1 < bestPath2) {
        return 1;
      } else {
        return -1;
      }
      // } else {
      //   return 1;
      // }
    });
    return currentUser;
  },
};
Vue.prototype.$GF = GF;
